import React from "react";
import {TaskItem} from "../TaskItem/TaskItem";


export function TaskList(props) {

    function check (id){
        props.checkTask(id);
    }

    function del (id){
        props.deleteTask(id);
    }



    function onClickPriority(type,id){
        props.onClickPriority(type,id)
    }
    //update(props.items,props.currentCategory)
    return props.items !== undefined ? <div className='myTasks'>
        {props.items.map(i => <TaskItem  onClickPriority={onClickPriority} key={i.id} item={i} deleteTask={del} checkTask={check}/>)}
    </div> : <></>;
}

