import React, {useRef} from "react";
import PropTypes from "prop-types";

export function TaskAdd(props) {
    const inputEl = useRef();

    function handleKeyPress(event) {
        if (event.key === "Enter" && inputEl.current.value !== "") {
            props.onNoteAdded({
                val:inputEl.current.value,
                cat:props.currentCategory
            })
            inputEl.current.value = '';
        }
    }

    return <main className="main">
        <div className="myflexInput">
            <div className="arrow" data-testid="selectAll" onClick={() => props.onSelectAll()}/>
            <input data-testid="txtNote" type="text" ref={inputEl}
               onKeyPress={handleKeyPress} className="input" placeholder="What needs to be done?" />
        </div>
    </main>
}

TaskAdd.propTypes = {
    onNoteAdded: PropTypes.func.isRequired,
    onSelectAll: PropTypes.func.isRequired
}