import React, {useRef} from "react";
import './Sidebar.css'
import {Link} from "react-router-dom";
import {faList, faTools} from "@fortawesome/free-solid-svg-icons"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
export const EventAddCat = 'addCategories';
export const EventDelCat = 'deleteCat';
export const EventDelCatItems = 'deleteCatItems';

export function Sidebar(props) {
    const inputEl = useRef();



    function handleKeyPress(event) {
        if (event.key === "Enter") {
            props.eventDispatcher(EventAddCat,inputEl.current.value)
            // setCategoryList([...categoryList,inputEl.current.value])
            inputEl.current.value = '';
        }
    }

    function chooseCategory(val) {
        props.chooseCategory(val)
        if(props.currentWidth < 919){
            props.onBurgerClick()
        }
    }

    function onClickFilter(type){
        props.onClickFilter(type);
        props.onBurgerClick()

    }

    function deleteCategory(id, cat){
        console.log(props.items)
        props.eventDispatcher(EventDelCatItems,cat);
        props.eventDispatcherCat(EventDelCat,id);
        // deleteCategoryItems(props.currentCategory)
    }



    return <div>
                <div className={props.burger === true ? "sidebar active" : "sidebar"}>
                    <h2 className="sidebar-title"><FontAwesomeIcon className="check-font" icon={faList} />Categories</h2>
                    <div className="sidebar_section">
                        <input data-testid="txtNote" type="text" ref={inputEl}
                               onKeyPress={handleKeyPress} className="sidebar_input" placeholder="Create your own category" />
                        {props.items.map(item => {return (<div key={Date.now() + Math.round(Math.random() * 99999999)}>
                            <div className="button-section">
                                <Link to={'/'+item.val}> <div className="myButton" onClick={() => chooseCategory(item.val)}> {item.val} </div></Link>
                                <Link to={'/filter'}><span className="cat-times" onClick={() => deleteCategory(item.id, item.val)}>×</span></Link>
                            </div>
                        </div>)})}
                    </div>
                    <h2 className="sidebar-title"><FontAwesomeIcon className="check-font" icon={faTools} />Filters</h2>
                    <div className="button-section">
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('all')}>All</div></Link>
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('active')}>Active</div></Link>
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('completed')}>Completed</div></Link>
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('highPriority')}>High priority</div></Link>
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('middlePriority')}>Middle priority</div></Link>
                        <Link to={'/filter'}><div className="myButton" onClick={()=> onClickFilter('lowPriority')}>Low priority</div></Link>
                    </div>
                </div>



            </div>;
}
