
const initialState = localStorage.getItem('categories') ? JSON.parse(localStorage.getItem('categories')) : []
const genId = () => Date.now() + Math.round(Math.random() * 99999999)

const todoCategories = (state = initialState, action) => {
    switch(action.type){
        case "TODO_ADD_CATEGORIES":
            return [...state, {val:action.val, id: genId()}]
        case "TODO_DELETE_CAT":
            const index = {};
            state.map((item,i) => item.id === action.id ? index.id = i : '')
            return [...state.slice(0, index.id), ...state.slice(index.id + 1)]
        case "TODO_SEARCH_LIST":
            console.log(action.val);
            return state
        default:
            console.log("ERROR");

    }
    return state;
}

const todoPersistentCategories = (state = initialState, action) => {
    const newState = todoCategories(state, action);
    localStorage.setItem('categories', JSON.stringify(newState));
    return newState;
}

export default todoPersistentCategories