import React from "react";
import {TaskList} from "../TaskList/TaskList";
import {EventTaskCheck} from "../Tasks/Tasks";
import {EventTaskDelete} from "../Tasks/Tasks";
import {EventClickPriority} from "../Tasks/Tasks";


function multiFilter(items, activeFilter, val){
    switch (activeFilter) {
        case 'all':
            return items

        case 'active':
            return items.filter(i => !i.completed)

        case 'completed':
            return items.filter(i => i.completed)

        case 'highPriority':
            return items.filter(i => i.priority === 'high');

        case 'middlePriority':
            return items.filter(i => i.priority === 'mid');

        case 'lowPriority':
            return items.filter(i => i.priority === 'low');

        case 'search':
            return items.filter(s => s.text.toLowerCase().search(val.val.toLowerCase()) !==  -1);
        default:
            break;
    }
}

export function filterItems(items, activeFilter, val) {
    // let myItems = [];
    // for(let i = 0; i < activeFilter.length; i++){
    //     myItems = [...multiFilter(items, activeFilter[i])];
    // }
    return multiFilter(items, activeFilter, val)

}



function Filter(props) {


    function check (id){
        props.eventDispatcher(EventTaskCheck, id);
    }

    function del (id){
        props.eventDispatcher(EventTaskDelete, id)
    }

    function onClickPriority(type, id){
        props.eventDispatcher(EventClickPriority, type, id)
    }


    return (
        <>
            <h1 className={props.burger === true ? "title-little active" : "title-little"}>{props.type.toUpperCase()}</h1>
            <div className={props.burger === true ? "shadow active" : "shadow"}>
                <TaskList onClickPriority={onClickPriority} currentCategory={props.currentCategory} deleteTask={del} checkTask={check}
                          items={filterItems(props.items, props.type, props.searchValue)}
                 />
            </div>
        </>
    );
}

export default Filter;