import React from "react";
import PropTypes from 'prop-types';
import "./TaskFilter.css"

export function TaskFilter(props) {

    function click(type) {
        if (props.onFilterChange) {
            props.onFilterChange(type)
        }
    }

    function clear(cat){
        props.clearItems(cat)
    }

    return (<footer className="myFooter">
        <div className="Row">
            <button onClick={() => click('all')}>All</button>
            <button onClick={() => click('active')}>Active</button>
            <button onClick={() => click('completed')}>Completed</button>
            <button className="clear" onClick={() => clear(props.currentCategory)}>Clear</button>

        </div>
    </footer>)
}

TaskFilter.propTypes = {
    onFilterChange: PropTypes.func.isRequired
}