const initialState = localStorage.getItem('todos') ? JSON.parse(localStorage.getItem('todos')) : []
const genId = () => Date.now() + Math.round(Math.random() * 99999999)

const todoReducer = (state = initialState, action) => {
    switch(action.type){
        case "TODO_ADD":
            return [...state, {text: action.obj.val, id: genId(), completed: false, category: action.obj.cat, priority: 'mid'}]
        case "TODO_COMPLETE":
            return state.map(item => item.id === action.id ? { ...item, completed: !item.completed} : item)
        case "TODO_DELETE":
            const index = {};
            state.map((item,i) => item.id === action.id ? index.id = i : '')
            return [...state.slice(0, index.id), ...state.slice(index.id + 1)]
        case "TODO_SELECT_ALL":

            const categoryItems = state.filter(item => item.category === action.category)
            const truthAmount = categoryItems.filter(i => i.completed).length
            const allAmount = categoryItems.length;
            console.log(this)
            if(truthAmount === allAmount){
                return state.map(item => item.category === action.category ? { ...item, completed: false} : item)
            } else {
                return state.map(item => item.category === action.category ? { ...item, completed: true} : item)
            }
        case "TODO_CLEAR":
            const arr = state.filter(item => item.category !== action.cat);
            const arr2 = state.filter(item => item.category === action.cat && item.completed === false)
            return [...arr,...arr2];
        case "TODO_PRIORITY":
            return state.map(item => item.id === action.id ? { ...item, priority: action.val} : item)
        case "TODO_DELETE_CAT_ITEMS":
            return state.filter(item => item.category !== action.cat)
        default:
//             console.error('Unknown action', action);
            break;
    }
    return state;
}

const todoPersistentReducer = (state = initialState, action) => {
    const newState = todoReducer(state, action);
    localStorage.setItem('todos', JSON.stringify(newState));
    return newState;
}

export default todoPersistentReducer