import React from "react";
import './TaskItem.css'


export function TaskItem(props) {


    function handleTaskCheck() {
        // dispatch(allActions.todos.complete(props.item.id))
        props.checkTask(props.item.id);
    }

    function handleDeleteTask() {
        // dispatch(allActions.todos.del(props.item.id))
        props.deleteTask(props.item.id);
    }

    function onClickPriority(type,id){
        // console.log(props.item.id)
        props.onClickPriority(type,id);
    }

    return <div className="panel">

        <label className="container">
            <input className="toggle" type="checkbox" checked={props.item.completed}
                   onChange={handleTaskCheck}/>
            <span className="checkmark"/>
        </label>

        <div className="dropdown">
            <h3 className="current-priority">{props.item.priority}</h3>
            <div className='dropdown-content'>
                <div className="dropdown-content__high" onClick={() => onClickPriority('high',props.item.id)}>high</div>
                <div className="dropdown-content__mid" onClick={() => onClickPriority('mid',props.item.id)}>mid</div>
                <div className="dropdown-content__low" onClick={() => onClickPriority('low',props.item.id)}>low</div>
            </div>
        </div>

        <span className={props.item.completed ? "items_active" : "items"}>{props.item.text}</span>
        <p className="times" onClick={handleDeleteTask}>×</p>
    </div>
}
