import React, {useState} from "react";
import {Tasks} from "./Tasks/Tasks";
import {Sidebar} from "./Sidebar/Sidebar";
import allActions from "../actions";
import Filter from "./Filter/Filter"
import {useDispatch, useSelector} from "react-redux";
import creatCat from './create_cat.jpg'
import shop from './shop.jpg'
import filters from './filters.jpg'
import priorities from './priorities.jpg'
import {
    Switch,
    Route
} from "react-router-dom";
import {Navbar} from "./Navbar/Navbar";
import {useWindowDimensions} from './WindowDimensions'
import './HomePage.css'

export function filterItems(items, activeFilter) {
    switch (activeFilter) {
        case 'all':
            return items;

        case 'active':
            return items.filter(i => !i.completed)

        case 'completed':
            return items.filter(i => i.completed)

        default:
            break;
        //     console.error('Unknown filter', activeFilter);
    }
}


export function Layout(props) {
    const [currentCategory, setCurrentCategory] = useState()
    const items = useSelector(state => state.todos)
    const items2 = useSelector(state => state.todos2)
    const dispatch = useDispatch()
    const [myDefault, setDefault] = useState(false);
    const [filter, setFilter] = useState('')
    const [currentFilter, setCurrentFilter] = useState('');
    const [searchValue, setSearchValue] = useState('');
    const [burger, setBurger] = useState(false);
    const { width } = useWindowDimensions();


    function fireEvent(...args) {
        const event = args[0];
        const params = args.splice(1);

        dispatch(allActions.todos[event].apply(this, params))
    }

    function fireEventCat(...args) {
        const event = args[0];
        const params = args.splice(1);

        dispatch(allActions.todos[event].apply(this, params))
    }


    function chooseCategory(val){
        setDefault(true)
        setCurrentCategory(val);
        setCurrentFilter('');
    }


    function searchList(val) {
        setDefault(true)
        setFilter('search')
        setCurrentFilter('filter');
        setSearchValue(val)

    }

    function onClickFilter(type){
        setDefault(true)
        setFilter(type)
        setCurrentFilter('filter');
    }

    function onBurgerClick() {

        setBurger(!burger)
    }


    return (
        <div className="App">

            <Navbar searchList={searchList} onBurgerClick={onBurgerClick} burger={burger}/>

            <Sidebar eventDispatcherCat={fireEventCat} eventDispatcher={fireEvent} currentCategory={currentCategory}
                     chooseCategory={chooseCategory} onBurgerClick={onBurgerClick} burger={burger}
                     items={items2} onClickFilter={onClickFilter} currentWidth={width}/>

            {myDefault === false ? <div className="homepage">
                <h1 className={burger === true ? "title active" : "title"}>How it works</h1>
                <div className="home-flex">
                    <div className="home-text">
                        <span>1. At the beginning create your own category, for example it can be "Shopping"</span>
                    </div>
                    <div>
                        <img className="pic-1" src={creatCat}/>
                    </div>
                </div>
                <div className="home-flex">
                    <div>
                        <img className="pic-1" src={shop}/>
                    </div>
                    <div className="home-text">
                        <span>2. Now you can add any task you have to do in the near future</span>
                    </div>
                </div>
                <div className="home-flex">
                    <div className="home-text">
                        <span>3. Also you can use any filters to faster search your tasks</span>
                    </div>
                    <div>
                        <img className="pic-1" src={filters}/>
                    </div>
                </div>
                <div className="home-flex">
                    <div>
                        <img className="pic-1" src={priorities}/>
                    </div>
                    <div className="home-text">
                        <span>4. In our application you can give your tasks priorities to specify their importance</span>
                    </div>
                </div>
            </div>
            : <></>}

            <Switch>

                <Route path={'/' + currentCategory}>
                    <Tasks eventDispatcher={fireEvent} burger={burger} currentCategory={currentCategory} items={items} />
                </Route>

                <Route path={'/' + currentFilter}>
                    <Filter eventDispatcher={fireEvent} burger={burger} searchValue={searchValue} type={filter} items={items}/>
                </Route >


            </Switch>
        </div>
    );
}