import React, {useRef} from "react";
import './Navbar.css'
import {Link} from "react-router-dom";
    import { faCheck } from "@fortawesome/free-solid-svg-icons"
    import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export function Navbar(props) {

    const inputEl = useRef();
    const inputEl2 = useRef();

    function handleKeyPress(event) {

        props.searchList({
            val:inputEl.current.value
        })
    }
    function handleKeyPress2(event) {


        props.searchList({
            val:inputEl2.current.value
        })
    }

    function onHandleChange(){

        props.onBurgerClick()
    }

    return <><div className="navbar">
        <div className="navbar__search">
            <Link to={'/filter'}>
                <div className="form__group field">
                    <input onClick={handleKeyPress2} ref={inputEl2} onChange={handleKeyPress2} type="input"
                           className="form__field" placeholder="search your task" name="search" id='search' required/>
                    {/*<label htmlFor="search" className="form__label">Search</label>*/}
                </div>
            </Link>
        </div>

        <div className="navbar__logo">
            <h3 className="logo__title"><FontAwesomeIcon className="check-font" icon={faCheck} />Add<span className="note">Note</span></h3>
        </div>
    </div>
        <div className={ props.burger ? "navbar__little-active" : "navbar__little"} >
           <div className="navbar__burger" onClick={onHandleChange}>
               <div className={ props.burger ? "first_child active" : "first_child"}></div>
               <div className={ props.burger ? "second_child active" : "second_child"}></div>
               <div className={ props.burger ? "third_child active" : "third_child"}></div>
           </div>

            <div className="navbar__logo">
                <h3 className="logo__title"><FontAwesomeIcon className="check-font" icon={faCheck} />Add<span className="note">Note</span></h3>
            </div>

            <div className="navbar__search">
                <Link to={'/filter'}>
                    <div className="form__group field">
                        <input onClick={handleKeyPress} ref={inputEl} onChange={handleKeyPress} type="input"
                               className="form__field" placeholder="search your task" name="search" id='search' required/>
                        {/*<label htmlFor="search" className="form__label">Search</label>*/}
                    </div>
                </Link>
            </div>
        </div>
    </>
}
