import React, {useState} from "react";
import {TaskAdd} from "../TaskAdd/TaskAdd";
import {TaskList} from "../TaskList/TaskList";
import {TaskFilter} from "../TaskFilter/TaskFilter";

export const EventNoteAdded = 'add';
export const EventSelectAll = 'selectAll';
export const EventClickPriority = 'priority';
export const EventTaskDelete = 'del';
export const EventTaskCheck = 'complete';
export const EventTasksClear = 'clear';

export function Tasks(props) {
    const [activeFilter, setFilterState] = useState('all');

    function click(type) {
        setFilterState(type)
    }

    function add(val){
        props.eventDispatcher(EventNoteAdded, val);
        //props.onNoteAdded(val)
    }

    function check(id){
        props.eventDispatcher(EventTaskCheck,id);
    }

    function del (id){
        props.eventDispatcher(EventTaskDelete,id)
    }

    function clear(cat){
        props.eventDispatcher(EventTasksClear,cat);
    }

    function onClickPriority(type, id){
        props.eventDispatcher(EventClickPriority, type, id)
    }

    function update(items,cat){
        return items.filter(item => item.category === cat)
    }

    function filterItems(items, activeFilter) {
        switch (activeFilter) {
            case 'all':
                return update(items,props.currentCategory);

            case 'active':
                return update(items.filter(i => !i.completed),props.currentCategory)

            case 'completed':
                return update(items.filter(i => i.completed),props.currentCategory)

            default:
                return [];
            //     console.error('Unknown filter', activeFilter);
        }
    }

    return (
        <>
            <h1 className={props.burger === true ? "title-little active" : "title-little"}>{props.currentCategory.toUpperCase()}</h1>
            <div className={props.burger === true ? "shadow active" : "shadow"}>
                <TaskAdd currentCategory={props.currentCategory} onNoteAdded={add}
                         onSelectAll={() => props.eventDispatcher(EventSelectAll,props.currentCategory)}/>
                <TaskList onClickPriority={onClickPriority} currentCategory={props.currentCategory} deleteTask={del}
                          checkTask={check} items={filterItems(props.items, activeFilter)}/>
                 <TaskFilter currentCategory={props.currentCategory} clearItems={clear}
                          onFilterChange={click}/>
            </div>
        </>
    );
}